.App {
  text-align: center;
  display: flex;
  flex-direction: row;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  font-family: 'Montserrat', sans-serif;
}

.Content {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.Body {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Title {
  font-weight: bold;
  font-size: xx-large;
}

.Item {
  display: flex;
  flex-direction: column;
  padding: 12px;
}

.Item-Title {
  font-weight: bold;
}

.Item-Details {
  display: flex;
  flex-direction: row;
}

.SubItem {
  padding: 6px;
  padding-left: 6px;
  padding-right: 6px;
}

.Logo {
  height: 25%;
  width: 25%;
}